import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import Joi from "joi-browser";
import {
  Navbar,
  NavbarBrand,
  Jumbotron,
  Nav,
  NavItem,
  Collapse,
  NavbarToggler,
  Modal,
  Button,
  Label,
  ModalHeader,
  ModalBody,
  Form,
  FormGroup,
  Input,
} from "reactstrap";

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isNavOpen: false,
      isModalOpen: false,
      username: "",
      password: "",
    };
    this.onToggle = this.onToggle.bind(this);
  }

  schema = {
    username: Joi.string().required(),
    password: Joi.string().required(),
  };

  onToggle = () => {
    this.setState({
      isNavOpen: !this.state.isNavOpen,
    });
  };
  handleChange = ({ currentTarget }) => {
    //const errors = validateChange(currentTarget);
    const value = currentTarget.value;
    const name = currentTarget.name;
    this.setState({ [name]: value });
  };

  handleLogin = (e) => {
    alert(
      "username: " + this.state.username + "password: " + this.state.password
    );
    e.preventDefault();
  };

  toggleModal = () => this.setState({ isModalOpen: !this.state.isModalOpen });
  render() {
    return (
      <React.Fragment>
        <Navbar dark expand="md">
          <div className="container">
            <NavbarToggler onClick={this.onToggle}></NavbarToggler>
            <NavbarBrand className="mr-auto" href="/">
              <img
                src="assets/images/logo.png"
                height="30"
                width="41"
                alt="Matrix"
              />
            </NavbarBrand>
            <Collapse className="ml-auto" navbar isOpen={this.state.isNavOpen}>
              <Nav navbar>
                <NavItem>
                  <NavLink to="/home" className="nav-link">
                    <span className="fa fa-home fa-lg"></span> Home
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink to="/aboutus" className="nav-link">
                    <span className="fa fa-info fa-lg"></span> About us
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink to="/dishes" className="nav-link">
                    <span className="fa fa-list fa-lg"></span> Menu
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink to="/contactus" className="nav-link">
                    <span className="fa fa-address-card fa-lg"></span> Contact
                    Us
                  </NavLink>
                </NavItem>
              </Nav>
              <Nav className="ml-auto">
                <NavItem>
                  <Button
                    onClick={this.toggleModal}
                    style={{ color: "#FFFFFF80" }}
                    outline
                  >
                    <span className="fa fa-sign-in fa-lg" />
                    Login
                  </Button>
                </NavItem>
              </Nav>
            </Collapse>
          </div>
        </Navbar>
        <Jumbotron className="jumbo">
          <div className="container">
            <div className="row row-header">
              <div className="col-12 col-sm-6">
                <h1>Matrix</h1>
                <p>
                  We take inspiration from the World's best cuisines, and create
                  a unique fusion experience. Our lipsmacking creations will
                  tickle your culinary senses!
                </p>
              </div>
            </div>
          </div>
        </Jumbotron>
        <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
          <ModalHeader toggle={this.toggleModal}>Login</ModalHeader>
          <ModalBody>
            <Form onSubmit={this.handleLogin}>
              <FormGroup>
                <Label htmlFor="username">Username</Label>
                <Input
                  onChange={this.handleChange}
                  id="username"
                  type="text"
                  name="username"
                  innerRef={(input) => (this.username = input)}
                >
                  username
                </Input>
              </FormGroup>
              <FormGroup>
                <Label htmlFor="password">Password</Label>
                <Input
                  onChange={this.handleChange}
                  id="password"
                  type="password"
                  name="password"
                  value={this.state.password}
                >
                  username
                </Input>
              </FormGroup>
              <Button type="submit" value="submit" color="secondary">
                Login
              </Button>
            </Form>
          </ModalBody>
        </Modal>
      </React.Fragment>
    );
  }
}

export default Header;
