import * as ActionTypes from "./ActionTypes";

export const Dishes = (
  state = {
    isLoading: true,
    dishes: [],
    error: null,
  },
  action
) => {
  switch (action.type) {
    case ActionTypes.ADD_DISHES:
      return {
        ...state,
        isLoading: false,
        dishes: action.payload,
        error: null,
      };
    case ActionTypes.LOADING_DISHES:
      return { ...state, isLoading: true, dishes: [], error: null };
    case ActionTypes.DISHES_FAILED:
      return { ...state, isLoading: false, dishes: [], error: action.payload };
    default:
      return state;
  }
};
