import React from "react";
import Loading from "./LoadingComponent";
import {
  Card,
  CardBody,
  CardImg,
  CardTitle,
  CardSubtitle,
  CardText,
} from "reactstrap";
import { FadeTransform } from "react-animation-components";
import { baseUrl } from "./../shared/baseUrl";
function Home({
  dishesIsLoading,
  dishes,
  dishesError,
  leaders,
  promotions,
  promosIsLoading,
  leadersIsLoading,
  promosError,
  leadersError,
}) {
  const RenderItems = ({ item, isLoading, error }) => {
    if (isLoading)
      return (
        <div className="container">
          <div className="row" style={{ textAlign: "center" }}>
            <Loading />
          </div>
        </div>
      );
    else if (error)
      return (
        <div className="container">
          <div className="row">{error}</div>
        </div>
      );
    else
      return (
        <FadeTransform
          in
          transformProps={{
            exitTransform: "scale(0.5) translateY(-50%)",
          }}
        >
          <Card>
            <CardImg src={baseUrl + item.image} alt={item.name}></CardImg>
            <CardBody>
              <CardTitle>{item.name}</CardTitle>
              {item.designation ? (
                <CardSubtitle>{item.designation}</CardSubtitle>
              ) : null}
              <CardText>{item.description}</CardText>
            </CardBody>
          </Card>
        </FadeTransform>
      );
  };

  return (
    <div className="container">
      <div className="row align-items-start" style={{ margin: "15px auto" }}>
        <div className="col-12 col-md m2">
          <RenderItems
            item={dishes}
            isLoading={dishesIsLoading}
            error={dishesError}
          />
        </div>
        <div className="col-12 col-md m2">
          <RenderItems
            item={leaders}
            isLoading={leadersIsLoading}
            error={leadersError}
          />
        </div>
        <div className="col-12 col-md m2">
          <RenderItems
            item={promotions}
            isLoading={promosIsLoading}
            error={promosError}
          />
        </div>
      </div>
    </div>
  );
}
export default Home;
