import React from "react";
import Menu from "./MenuComponent";
import DishDetail from "./DishdetailComponent";
import Header from "./Header";
import Footer from "./Footer";
import { Redirect, Route, Switch, withRouter } from "react-router-dom";
import Home from "./Home";
import ContactUs from "./ContactUs";
import About from "./AboutComponent";
import { connect } from "react-redux";
import {
  fetchComments,
  fetchDishes,
  fetchPromos,
  fetchLeaders,
  postComment,
  postFeedback,
} from "../redux/ActionCreators";
import { Component } from "react";
import { actions } from "react-redux-form";
import { TransitionGroup, CSSTransition } from "react-transition-group";

const mapStateToProps = (state) => {
  return {
    dishes: state.dishes,
    comments: state.comments,
    promotions: state.promotions,
    leaders: state.leaders,
  };
};

const mapDispatchToProps = (dispatch) => ({
  postComment: (dishId, rating, author, comment) =>
    dispatch(postComment(dishId, rating, author, comment)),
  resetFeedback: () => dispatch(actions.reset("feedback")),
  fetchDishes: () => dispatch(fetchDishes()),
  fetchPromos: () => dispatch(fetchPromos()),
  fetchComments: () => dispatch(fetchComments()),
  fetchLeaders: () => dispatch(fetchLeaders()),
  postFeedback: (values) => postFeedback(values),
});

class Main extends Component {
  componentDidMount() {
    this.props.fetchDishes();
    this.props.fetchComments();
    this.props.fetchPromos();
    this.props.fetchLeaders();
  }
  HomePage = () => {
    return (
      <Home
        dishes={this.props.dishes.dishes.filter((dish) => dish.featured)[0]}
        dishesIsLoading={this.props.dishes.isLoading}
        dishesError={this.props.dishes.error}
        promotions={
          this.props.promotions.promotions.filter((val) => val.featured)[0]
        }
        promosIsLoading={this.props.promotions.isLoading}
        leadersIsLoading={this.props.leaders.isLoading}
        promosError={this.props.promotions.error}
        leadersError={this.props.leaders.error}
        leaders={this.props.leaders.leaders.filter((val) => val.featured)[0]}
      />
    );
  };
  RenderDish = ({ match }) => {
    return (
      <DishDetail
        dish={
          this.props.dishes.dishes.filter(
            (dish) => dish.id === parseInt(match.params.dishId, 10)
          )[0]
        }
        comment={this.props.comments.comments.filter(
          (comment) => comment.dishId === parseInt(match.params.dishId, 10)
        )}
        postComment={this.props.postComment}
        isLoading={this.props.dishes.isLoading}
        error={this.props.dishes.error}
        commentsError={this.props.comments.error}
      />
    );
  };
  render() {
    return (
      <div>
        <Header />
        <TransitionGroup>
          <CSSTransition
            key={this.props.location.key}
            classNames="page"
            timeout={300}
          >
            <Switch>
              <Route path="/home" component={this.HomePage} />
              <Route
                path="/aboutus"
                component={() => <About leaders={this.props.leaders}></About>}
              />
              <Route
                exact
                path="/dishes"
                component={() => <Menu dishes={this.props.dishes} />}
              />
              <Route path="/dishes/:dishId" component={this.RenderDish} />
              <Route
                exact
                path="/contactus"
                component={() => (
                  <ContactUs
                    resetFeedbackForm={this.props.resetFeedback}
                    postFeedback={this.props.postFeedback}
                  />
                )}
              />
              <Redirect to="/home" />
            </Switch>
          </CSSTransition>
        </TransitionGroup>
        <Footer />
      </div>
    );
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Main));
